#planning-table {
	overflow: auto;
	width: 100%;
	height: calc( 100% - 40px );
	table {
		width: 100%;
		border-collapse: collapse;
		height: 100%;
	}
	tr {
		&:first-child {
			
			top: 0;
		}
	}
	th {
		font-size: 10px;
		color: #0c0c0c;
		background: #DFDFDF;
		border: 1px solid #D0D0D0;
		vertical-align: top;
		font-weight: 600;
		border-bottom: 4px solid #D0D0D0;
		padding-top: 6px;
		padding-left: 5px;
		padding-right: 5px;
		padding-bottom: 15px;
		position: sticky;
		top: 0;
		.fa-angle-down {
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			bottom: 0;
			font-size: 18px;
			height: 16px;
			width: 16px;
			color: #5B5B5B;
		}
		div {
			white-space: nowrap;
		}
	}
	td {
		border: 1px solid #D0D0D0;
		font-size: 10px;
		color: #7d7c7c;
		text-align: center;
		vertical-align: top;
		padding: 7px 2px 3px 2px;
		input[type="text"] {
			display: block;
			width: 100%;
			font-size: 10px;
			color: #7d7c7c;
			border: 1px solid #D0D0D0;
			height: 20px;
			text-align: center;
			margin-top: -3px;
		}
	}
}